<template>
  <div>
    <!----->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="this.getOrderList()"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getOrderList"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table
        border
        stripe
        :data="orderList"
      >
        <el-table-column
          type="index"
          label="#"
        ></el-table-column>
        <el-table-column
          label=""
          prop="order_number"
        ></el-table-column>
        <el-table-column
          label=""
          prop="order_price"
          width="140px"
        ></el-table-column>
        <el-table-column
          label=""
          prop="pay_status"
          width="140px"
        >
          <template slot-scope="scope">
            <el-tag
              type="success"
              v-if="scope.row.pay_status === '1'"
            ></el-tag>
            <el-tag
              type="danger"
              v-else
            ></el-tag>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="is_send"
          width="100px"
        ></el-table-column>
        <el-table-column
          label=""
          prop="create_time"
          width="140px"
        >
          <template slot-scope="scope">
            {{scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="150px"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showOrderDialog"
            ></el-button>
            <el-button
              size="mini"
              type="success"
              icon="el-icon-location"
              @click="showLocationDiaolog"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="orderVisible"
      width="50%"
      @close="orderDialogClosed"
    >
      <el-form
        :model="editOrderForm"
        :rules="editOrderFormRules"
        ref="editOrderFormRef"
        label-width="100px"
      >
        <el-form-item
          label=""
          prop="address1"
        >
          <el-cascader
            v-model="editOrderForm.address1"
            :options="cityData"
            clearablle
          ></el-cascader>
        </el-form-item>
        <el-form-item
          label=""
          prop="address2"
        >
          <el-input v-model="editOrderForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="orderVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="orderVisible = false"
        > </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressVisible"
      width="50%"
      @close="progressDialogClosed"
    >
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time"
        >
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="progressVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="progressVisible = false"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import cityData from './citydata.js'
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      orderList: [],
      total: 0,
      orderVisible: false,
      editOrderForm: {
        address1: [],
        address2: ''
      },
      editOrderFormRules: {
        address1: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        address2: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      cityData,
      progressVisible: false,
      //  
      progressInfo: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      console.log(res.data)
      this.orderList = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newNum) {
      this.queryInfo.pagenum = newNum
      this.getOrderList()
    },
    showOrderDialog() {
      this.orderVisible = true
    },
    orderDialogClosed() {
      this.$refs.editOrderFormRef.resetFields()
    },
    async showLocationDiaolog() {
      const { data: res } = await this.$http.get(`/kuaidi/${1106975712662}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.progressInfo = res.data
      this.progressVisible = true
    },
    progressDialogClosed() {
      this.progressInfo = []
    }
  }
}
</script>
<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>